}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: box-shadow .3s;
  display: flex;
  box-shadow: 0 4px 8px #0000001a;
}

.card:hover {
  box-shadow: 0 8px 16px #0003;
}

.card__image {
  justify-self: flex-start;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  align-self: flex-start;
  width: 166px;
  margin-bottom: 4px;
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.card__code {
  color: #616070;
  align-self: flex-start;
  margin-bottom: 16px;
  line-height: 14px;
  display: block;
}

.card__reviews {
  color: #060b35;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-bottom: 24px;
  line-height: 14px;
  display: flex;
}

.card__reviews-text {
  display: block;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price-attribute {
  color: #616070;
  font-size: 1.2rem;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 1.6rem;
  font-weight: 700;
}

.card__link {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  padding: 12px 0;
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__link:hover {
  color: #00acdc;
  background-color: #0000;
  border-color: #00acdc;
}

body {
  background-color: #fff;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Roboto, sans-serif;
  display: flex;
}
